@font-face {
  font-family: 'icons';
  src: url(data:application/font-woff2;charset=utf-8;base64,) format("woff2"), url(data:application/font-woff;charset=utf-8;base64,) format("woff");
  font-weight: normal;
  font-style: normal; }

.ka-icon-group-arrow-collapsed:before {
  content: '\e800'; }

.ka-icon-close:before {
  content: '\e801'; }

.ka-icon-group-arrow-expanded:before {
  content: '\e802'; }

.ka-icon-sort-arrow-down:before {
  content: '\e803'; }

.ka-icon-sort-arrow-up:before {
  content: '\e804'; }

.ka {
  overflow: hidden;
  background-color: white;
  font-size: 14px;
  display: flex;
  flex-direction: column; }

.ka-table-wrapper {
  height: 100%;
  width: 100%;
  overflow-y: auto; }

.ka-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse; }

.ka-thead-background {
  background-color: #F1F5F7;
  position: sticky;
  top: 0; }

.ka-thead-cell {
  padding: 15px 20px;
  color: #747D86; }

.ka-thead-cell-wrapper {
  display: flex; }

.ka-thead-cell-content-wrapper {
  width: 100%; }

.ka-thead-cell-content {
  width: 100%; }

.ka-tbody {
  height: 100%; }

.ka-thead-cell, .ka-cell {
  text-align: left; }

.ka-cell {
  padding: 8px 20px;
  line-height: 29px;
  color: #353C44; }

.ka-icon {
  font-weight: 100;
  font-size: 10px;
  font-family: 'icons'; }

.ka-input {
  max-width: 100%; }

.ka-cell-editor-validation-error .ka-input {
  background: #FFE7E7;
  border: 1px solid #FF0C0C; }

.ka-validation-message-container {
  position: absolute; }

.ka-validation-message {
  font-size: 12px;
  color: white;
  background-color: #FF0C0C;
  padding: 0 10px;
  max-width: 200px; }

.ka-empty-cell {
  width: 40px;
  min-width: 40px; }

.ka-tr {
  width: 100%; }

.ka-no-data-row {
  height: 100px;
  text-align: center; }

.ka-row {
  box-sizing: border-box;
  border-bottom: 2px solid #F9FBFC;
  border-top: 2px solid #F9FBFC; }

.ka-dragged-row {
  opacity: 0.5; }

.ka-drag-over-row {
  box-shadow: inset 0 7px 0px -4px #e1ebf0; }

.ka-dragged-row ~ .ka-drag-over-row {
  box-shadow: inset 0 -7px 0px -4px #e1ebf0; }

.ka-drag-over-column {
  box-shadow: inset 7px 0 0px -4px #e1ebf0; }

.ka-dragged-column ~ .ka-drag-over-column {
  box-shadow: inset -7px 0 0px -4px #e1ebf0; }

.ka-row-selected {
  background-color: #F7FcFd; }

.ka-group-row {
  background-color: #F9FBFC;
  box-sizing: border-box;
  border-bottom: 1px solid white;
  border-top: 1px solid white; }

.ka-group-cell {
  padding: 8px 10px; }

.ka-group-cell-content {
  display: flex;
  align-items: center; }

.ka-icon-group-arrow {
  padding: 10px 5px 10px 10px;
  margin-right: 5px;
  cursor: pointer; }

.ka-icon-sort {
  margin-left: 3px; }

.ka-pointer {
  cursor: pointer; }

.ka-filter-row-cell {
  padding: 0 20px 15px 20px; }

.ka-loading {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff88;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.ka-loading-active {
  position: relative; }

.ka-loading-icon {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  animation: typing 1s linear infinite alternate;
  position: relative;
  left: -20px; }

.ka-thead-cell-resize {
  user-select: none;
  width: 2px;
  cursor: col-resize;
  background-color: #d7e4eb;
  position: relative;
  left: 19px; }

@keyframes typing {
  0% {
    background-color: #747d86;
    box-shadow: 20px 0px 0px 0px rgba(116, 125, 134, 0.2), 40px 0px 0px 0px rgba(116, 125, 134, 0.2); }
  25% {
    background-color: rgba(116, 125, 134, 0.4);
    box-shadow: 20px 0px 0px 0px #747d86, 40px 0px 0px 0px rgba(116, 125, 134, 0.2); }
  75% {
    background-color: rgba(116, 125, 134, 0.4);
    box-shadow: 20px 0px 0px 0px rgba(116, 125, 134, 0.2), 40px 0px 0px 0px #747d86; }
  100% {
    box-shadow: none; } }

.ka-loading-text {
  margin-top: 15px;
  color: #353C44; }

.ka-paging-pages {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

.ka-paging-page-index {
  cursor: pointer;
  padding: 5px;
  margin: 10px 5px;
  min-width: 18px;
  border-radius: 50%;
  text-align: center; }

.ka-paging-page-index-active {
  background-color: #F1F5F7;
  font-weight: bold;
  color: #747D86; }
